.tags {
  display: inline-block;
  list-style-type: none;
}
.tags:first-child {
  margin-left: 0px;
  padding-left: 0px;
}
.tag {
  display: inline-block;
  padding: 0.5em;
  margin-top: 0em;
  margin: 0.5em;
  border-radius: 5px;
  background-color: #d7d7d7;
  user-select: none;
  color: black;
}
.tag-link {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
