.social {
  display: inline-block;
  list-style-type: none;
}
.social:first-child {
  margin-left: 0px;
  padding-left: 0px;
}
.social-element {
  display: inline-block;
  padding: 0.5em;
  margin: 0.5em;
  border-radius: 5px;
  background-color: #d7d7d7;
  user-select: none;
  cursor: pointer;
}
.social-element-link {
  color: black;
  text-decoration: none;
}
